.dot-item{
	cursor: pointer;
	margin: {
		left: 15px;
		right: 15px;
	}
	
	@media(min-width: $screen-md){
		width: calc(100% / 6);	
	}

	/* image */
	&__image{		
		margin: {
			left: auto;
			right: auto;
			bottom: 20px;
		}

		overflow: hidden;
		border-radius: 50%;

		background: {
			position: 50%;
			repeat: no-repeat;
			size: cover;
		}

		/* nivea dots */
		&--nivea-film{
			background-image: url('../dist/images/nivea-circle-film.jpg');
		}
		&--nivea-ad{
			background-image: url('../dist/images/nivea-circle-ad.jpg');
		}
		&--nivea-photo{
			background-image: url('../dist/images/nivea-circle-photo.jpg');
		}

		@media(max-width: $screen-md-max){
			display: none;
		}
		@media(min-width: $screen-md)and(max-width: $screen-xl-max){
			width: 120px;
			height: 120px;
		}
		@media(min-width: $screen-xl){
			width: 170px;
			height: 170px;
		}
	}
	
	/* title */ 
	&__title{
		margin: 0;
		text: {
			align: center;
			transform: uppercase;
		}

		font-weight: normal;
		color: $color-text;
		@media(max-width: $screen-sm-max){
			font-size: 14px;
		}
		@media(min-width: $screen-sm)and(max-width: $screen-lg-max){
			font-size: 21px;
		}
		@media(min-width: $screen-lg){
			font-size: 36px;
		}
	}
}